<template>
  <div class="container">
    <nav class="nav">
      <div class="nav-left">
        <a class="nav-item">
          <h1 class="title">Vue-Gravatar</h1>
        </a>
      </div>

      <span class="nav-toggle" :class="{ 'is-active': isMenuShown }" @click="isMenuShown = !isMenuShown">
        <span></span>
        <span></span>
        <span></span>
      </span>

      <div class="nav-right nav-menu" :class="{ 'is-active': isMenuShown }">
        <a href="https://github.com/JiriChara/vue-gravatar" class="nav-item">
          <span class="icon"><i class="fa fa-github"></i></span> GitHub
        </a>
      </div>
    </nav>

    <div class="section content">
      <div class="columns" id="app">
        <div class="column">
          <div>
            <v-gravatar
              :email="email"
              :default-img="defaultImage"
              :size="parseInt(size, 10)" />
          </div>

          <div class="field">
            <label class="label">Gravatar E-Mail</label>
            <input class="input is-fullwidth" type="email" v-model="email" />
          </div>

          <div class="field">
            <label class="label">Default Image</label>

            <span class="select is-fullwidth">
              <select v-model="defaultImage">
                <option value="404">404</option>
                <option value="mm">mm</option>
                <option value="identicon">identicon</option>
                <option value="monsterid">monsterid</option>
                <option value="wavatar">wavatar</option>
                <option value="retro">retro</option>
                <option value="blank">blank</option>
              </select>
            </span>
          </div>

          <div class="field">
            <label class="label">Image Size</label>

            <span class="select is-fullwidth">
              <select v-model="size">
                <option value="80">80</option>
                <option value="120">120</option>
                <option value="240">240</option>
                <option value="500">500</option>
              </select>
            </span>
          </div>

          <div class="field">
            <label class="label">Image Rating</label>

            <span class="select is-fullwidth">
              <select v-model="rating">
                <option value="g">g (suitable for display on all websites with any audience type)</option>
                <option value="pg">pg (may contain rude gestures, provocatively dressed individuals, the lesser swear words, or mild violence)</option>
                <option value="r">r (may contain such things as harsh profanity, intense violence, nudity, or hard drug use)</option>
                <option value="x">x (may contain hardcore sexual imagery or extremely disturbing violence)</option>
              </select>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        email: 'foo@bar.com',
        defaultImage: 'retro',
        size: 80,
        rating: 'g',
        isMenuShown: false
      }
    }
  }
</script>

<style lang="scss">
  @import './css/main';
</style>



